@import url("https://fonts.googleapis.com/css?family=Montserrat");

.title {
  margin-left: 40px;
  margin-top: 30px;
  font-family: "Montserrat";
  font-size: 26px;
  font-style: normal;
  font-weight: 600;
  line-height: 43.88px;
}
.header {
  display: flex;
  width: 100%;
  height: 102px;
  margin-top: 40px;

  background-color: #396afc0d;
}

.search {
  width: 297px;
  height: 37px;

  text-align: center;
  font-style: normal;
  font-weight: 400;
  font-size: 12px;
  line-height: 18px;
  font-family: "Montserrat";

  color: rgba(2 1 79 0.5);
  border-radius: 10px;
  border: none;

  background: #ffff;
  margin-top: 33px;
  margin-left: 40px;
}
.search:focus {
  outline: none;
}

.dropdown {
  background-color: #ffff;
  width: 156px;
  height: 37px;
  justify-content: center;
  top: 10px;

  border-radius: 10px;
}
@media only screen and (max-width: 530px) {
  .search {
    position: relative;
    top: 0;
    width: 90%;

    margin: auto;
  }
  .header {
    max-width: 100%;
  }
  .Icon-search {
    z-index: 9;
  }

  .Icon-orden {
    display: none;
  }
  .dropbox-container {
    display: none;
  }
}
/* @media screen and (max-width: ) {
} */
.Icon-search {
  position: absolute;
  z-index: 1;
  text-align: center;

  top: 158px;
  left: 8%;
  color: #396afc;
  margin: 0;
}
.dropbox-container {
  position: relative;
  left: 37%;
  top: 25px;
}
.select-section {
  /* padding: 12px; */
  height: 37px;
  text-align: center;
  color: #02014f;
  border: none;
  border-radius: 10px;
  width: 230px;
  background-color: #ffff;

  margin: 10px;
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 600;
  font-size: 12px;
  line-height: 18px;
}
.select-orden {
  padding: 12px;
  text-align: center;
  color: #02014f;
  border: none;
  width: 150px;
  background-color: #ffff;

  border-radius: 10px;

  margin: 10px;
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 600;
  font-size: 12px;
  line-height: 18px;
  cursor: pointer;
}
.select-section ::-ms-expand {
  outline: none;
  border: none;
}
.select-orden::-ms-expand {
  outline: none;
  border: none;
  text-align: center;
}
.select-selection option {
  cursor: pointer;
}
.Icon-orden {
  position: absolute;
  top: 21px;
  margin-left: 2px;
  color: #02014f;
  left: 265px;
}
.Icon-depart {
  color: #02014f;

  position: absolute;
  margin-left: 15px;
  top: 20px;
}
@media only screen and (min-width: 531px) and (max-width: 991px) {
  .search {
    position: relative;

    top: 0;
    width: 40%;
    left: 0;
    margin: auto;
  }
  .header {
    max-width: 100%;
  }
  .Icon-search {
    position: absolute;
    left: 15%;
    z-index: 9;
  }

  .Icon-orden {
    display: none;
  }
  .dropbox-container {
    left: 0%;
  }
}
